import React, { Component, Fragment } from 'react'
import avatar from '../../../assets/lf.jpg'
import {
  Card,
  Tag,
  Divider,
  Col
} from 'antd'
import './index.less'
import api from '../../../api'
import { color } from '../../../utils'

class SiderCustom extends Component {
  constructor(props) {
    super(props)
    this.state = {
      tags: [],
      articleData: []
    }
  }
  componentDidMount() {
    this.getTags()
    this.getArticleList()
  }
  async getArticleList () {
    const { data, code } = await api.get('/article/list', {pageNo:1, pageSize: 5})
    code === 1000 && this.setState({articleData: data})
  }
  async getTags () {
    const {data, code} = await api.get('tag/list/all')
    code === 1000 && this.setState({tags: data})
  }
  handleDetail (id) {
    this.props.history.push(`/web/detail/${id}`)
  }
  handleTags (id, name) {
    this.props.history.push({pathname: `/web/archive/${id}`, state: {name: name}})
  }
  render() {
    const list = this.state.articleData.map(v => (
      <li key={v.id} onClick={ this.handleDetail.bind(this, v.id) }>
        {v.title}
      </li>
    ))
    return (
      <Fragment>
        <Col
          lg={0}
          md={0}
          xs={24}>
          <div className="mobile-sider-container">
            <div className="tags-wrapper">
              <Card bordered={false}>
                <Divider orientation="left">标签</Divider>
                <div className="tags-content">
                  {
                    this.state.tags.map(v => (
                      <Tag
                        key = {v.id}
                        color={color[Math.floor(Math.random()*color.length)]}
                        onClick = {this.handleTags.bind(this, v.id, v.name)}
                      >
                        {v.name}
                      </Tag>
                    ))
                  }
                </div>
              </Card>
            </div>
          </div>
        </Col>
        <Col
          lg={{ span: 5, offset: 1 }}
          md={{ span: 6, offset: 1 }}
          xs={{ span: 0 }}
        >
          <div className="sider-container">
            <div className="admin-info">
              <header>
                <img src={avatar} alt="avatar" title='前路坦荡荡，人生不迷茫，奥里给'/>
              </header>
              <p className="admin-name">
                JsonCode
              </p>
              <p className="admin-desc">
                前端练习时长3年的练习生 喜欢唱，跳，RAP，篮球
                <br />
                前端摸鱼人员，全村最靓的仔
              </p>
            </div>
            <div className="recent-article">
              <Card bordered={false}>
                <Divider orientation="left">最近文章</Divider>
                <ul className="recent-list">
                  { list }
                </ul>
              </Card>
            </div>
            <div className="tags-wrapper">
              <Card bordered={false}>
                <Divider orientation="left">标签</Divider>
                <div className="tags-content">
                  {
                    this.state.tags.map(v => (
                      <Tag
                        key = {v.id}
                        color={color[Math.floor(Math.random()*color.length)]}
                        onClick = {this.handleTags.bind(this, v.id, v.name)}
                      >
                        {v.name}
                      </Tag>
                    ))
                  }
                </div>
              </Card>
            </div>
          </div>        
        </Col>
      </Fragment>
    )
  }
}

export default SiderCustom