import React, { Component } from 'react'
import { 
  Layout,
  Col,
  BackTop
} from 'antd'
import {
  Route
} from 'react-router-dom'
import HeaderCustom from '../header'
import SiderCustom from '../sider'
import routes from '../../../Router/web'
import './index.less'


const { Content, Footer } = Layout

class Index extends Component {
  constructor(props) {
    super(props)
    this.state = {
      collapsed: false
    }
  }
  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };
  componentDidMount () {
    document.title = 'JsonCode的博客'
  }
  render() {
    const contentHeight = document.body.clientHeight - 64 -62
    return (
      <div>
      <Layout className="wrapper">
        
        <HeaderCustom {...this.props}></HeaderCustom>
        <Layout className="wrapper-container">
          <Layout className="wrapper-content">
            <Content style={{ paddingTop: 24, margin: 0, minHeight: contentHeight, height: '100%', overflow: 'initial'}}>
              <SiderCustom {...this.props} />
              <Col
                lg={{ span: 16, offset: 1 }}
                md={{ span: 16, offset: 1 }}
                xs={{ span: 24 }}
                className="about-wrapper"
              >
                {
                  routes.map(({ path, key, component, ...props }, index) => (
                    <Route key={index}
                      exact
                      path={path}
                      component={component}
                      {...props}
                    />
                  ))
                }
              </Col>
            </Content>
          </Layout>
          <Footer style={{textAlign: 'center'}}>
            Copyright © JsonCode 2020
          </Footer>
        </Layout>
      </Layout>
      <BackTop visibilityHeight='100' />
      </div>
    )
  }
}

export default Index