import React, { Component } from 'react'
import { 
  Layout,
  Row,
  Col,
  Menu,
  Icon,
  Dropdown
} from 'antd'
import './index.less'
import { Link } from 'react-router-dom'
import menus from '../../../Router/web'
const { Header } = Layout

class HeaderCustom extends Component {
  constructor(props) {
    super(props)
    this.state = {
      visible: false
    }
  }
  handleClickBars () {
    this.setState({visible: true})
  }
  render() {
    const key = sessionStorage.getItem('webKey') || '0'

    const list = menus.filter(v => v.menu)

    const menuList = (list) => {
      return (
        <Menu mode="horizontal" defaultSelectedKeys={[key]}>
          {
            list.map((item, i) => {
              return (
                <Menu.Item key={i} onClick={ () => sessionStorage.setItem('webKey', String(i)) }>
                  <Link to={item.path}>
                    <Icon type={item.icon} />
                    <span className="nav-text">{item.title}</span>
                  </Link>
                </Menu.Item>   
              )
            })
          }
        </Menu>
      )
    }
    
    return (
      <Header className="header-container">
        <Row>
          <Col lg={6} md={6} xs={22}>
            <div className="logo"><Icon type="smile" theme="twoTone" /> JsonCode的博客</div>
          </Col>
          <Col lg={12} md={12} xs={0} className='mobile'>
              { menuList(list) }
          </Col>
          <Col className="dropdown-container" xs={2} sm={2} md={0} lg={0} xl={0}>
            <Dropdown
              overlayClassName='header-dropdown'
              trigger={['click']}
              overlay={menuList(list)}
              getPopupContainer={() => document.querySelector('.header-container')}>
              <Icon type='menu-o' className='header-dropdown-icon' />
            </Dropdown>
          </Col>
        </Row>
      </Header>
    )
  }
}

export default HeaderCustom